"use client";
import Link from "next/link";
import { usePathname } from "next/navigation";

const topics = ["React", "NodeJS", "MongoDB", "ExpressJS"];

export default function Sidebar() {
  const pathname = usePathname();
  return (
    <aside className="w-64 bg-gray-900 text-white min-h-screen p-5">
      <h2 className="text-xl font-bold mb-6">PreparationStreet</h2>
      <ul className="space-y-2">
        {topics.map((topic) => (
          <li key={topic}>
            <Link
              href={`/preparationstreet/${topic.toLowerCase()}`}
              className={`block px-4 py-2 rounded hover:bg-gray-700 ${
                pathname === `/preparationstreet/${topic.toLowerCase()}` ? "bg-gray-800" : ""
              }`}
            >
              {topic}
            </Link>
          </li>
        ))}
      </ul>
    </aside>
  );
}
